* {
  box-sizing: border-box;
}
/* Inline your critical styles */
html, body {
  /* Define app variables */
  --app-bg-color: #fafafa;
  --app-text-color: #313131;
  --app-card-color: #ffffff;
  --app-grey-color: #E2E2E2;
  --app-header-shadow: 0 0 4px rgba(0,0,0,.14), 0 4px 8px rgba(0,0,0,.28);

  min-height: 100%;
  margin: 0 auto;
  padding: 0;
  background-color: var(--app-bg-color);
  font-size: 16px;
  font-family: sans-serif;
  color: var(--app-text-color);
}


.root{
	display: flex;
	background-color: #2d84cc;
	align-items: center;
	justify-content: space-between;
	padding: 1em;
}

.logo{
	margin: 0;
	color: #fff;
	font-size: 14pt;
}



.root_Tweet_item{
	background-color: #fff;
	border:1px solid #ccc;
	border-radius: 5px;
	padding: 1em;
	margin: 0.5em;
}

.text{
	font-size: 16pt;
	font-weight: 200;
}

.user{
	display: flex;
	align-items: center;
}

.avatar{
	width: 34px;
	height: 34px;
	border-radius: 50%;
}
.user_card_P
{
	width: 100%;
	height: 100%;
}

.avatar_cardVersion{
	width: 40%;
	display: block;
  	margin-left: auto;
  	margin-right: auto;
	border-radius: 50%;
}

.displayName{
	font-weight: bold;
	padding: 0.5em;
}

.username{
	color: #aaa;
}

.date{
	color: #aaa;
	padding: 0.5em;
	font-size: 8pt;
}

.buttons{
	display: flex;
	justify-content: flex-start;
	color: #aaa;
}

.icon_reply{
	margin-right: 3em;
}

.num{
	font-size: 10pt;
	margin-right: 3em;
	margin-left: 0.25em;
}

.space{
	margin-right: 3em;
}

.rtGreen{
	color: #20883f;
	margin-right: 3em;
}

.favYellow{
	color: #e2c400;
}

.root_listTweet{
  margin: 0.5em;
}

.tweet_text{
	border-radius: 5px;
	border: 1px solid #ddd;
	width: 90%;
	height: 5em;
	padding: 0.75em;
	margin: 1em 0.5em;
}

.tweet_button{
	display: flex;
	justify-content: flex-end;
}

.tweet_button > button {
	border-radius: 5px;
	padding: 0.5em 0.75em;
	margin: 0.25em;
}

.tweet_close{
	background-color: #fff;
	border: 1px solid #ddd;
}

.tweet_send{
	background-color: #2d84cc;
	border: 1px solid #226aa6;
	color: #fff;
}

.open_button_tweet{
	background-color: #2d84cc;
	color: #fff;
	border: 1px solid #1f6fb1;
	border-radius: 5px;
	margin: 0 1em;
	padding: 0.5em 0.75em;
}

.home_avatar{
	border-radius: 50%;
	width: 32px;
	height: 32px;
}

.home_username{
	color: #666;
}


/* Add padding to containers */
.container {
	padding: 16px;
	background-color: white;
	width: 70%;
	margin: auto;
  }
  
  /* Full-width input fields */
  input[type=text], input[type=password], textarea {
	width: 100%;
	padding: 15px;
	margin: 5px 0 22px 0;
	display: inline-block;
	border: none;
	background: #f1f1f1;
  }
  
  input[type=text]:focus, input[type=password]:focus {
	background-color: #ddd;
	outline: none;
  }
  
  /* Overwrite default styles of hr */
  hr {
	border: 1px solid #f1f1f1;
	margin-bottom: 25px;
  }
  
  /* Set a style for the submit button */
  .registerbtn {
	background-color: #4CAF50;
	color: white;
	padding: 16px 20px;
	margin: 8px 0;
	border: none;
	cursor: pointer;
	width: 100%;
	opacity: 0.9;
  }
  
  .registerbtn:hover {
	opacity: 1;
  }
  
  /* Add a blue text color to links */
  a {
	color: rgb(255, 30, 79);
  }


  .signoutbutton{
	background-color: #2d84cc;
	color: #fff;
	border: 1px solid #1f6fb1;
	border-radius: 5px;
	margin: 0 1em;
	padding: 0.5em 0.75em;
}
  

ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
	background-color: rgb(224, 213, 213);
  }
  
  li {
	float: left;
  }
  
  li a {
	display: block;
	color: white;
	text-align: center;
	padding: 14px 16px;
	text-decoration: none;
  }
  
  li a:hover:not(.active) {
	background-color: #111;
  }
  

  .card {
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
	max-width: 300px;
	margin: auto;
	text-align: center;
  }
  
  .title {
	color: grey;
	font-size: 18px;
  }
/*

  	messagerie css

*/


.inbox_people {
	background: #f8f8f8 none repeat scroll 0 0;
	float: left;
	overflow: hidden;
	width: 40%; border-right:1px solid #c4c4c4;
  }
  .inbox_msg {
	border: 1px solid #c4c4c4;
	clear: both;
	overflow: hidden;
  }
  .top_spac{ margin: 20px 0 0;}
  
  
  .recent_heading {float: left; width:40%;}
  .srch_bar {
	display: inline-block;
	text-align: right;
	width: 60%; 
  }
  .headind_srch{ padding:10px 29px 10px 20px; overflow:hidden; border-bottom:1px solid #c4c4c4;}
  
  .recent_heading h4 {
	color: #05728f;
	font-size: 21px;
	margin: auto;
  }
  .srch_bar input{ border:1px solid #cdcdcd; border-width:0 0 1px 0; padding:2px 0 4px 6px; background:none;}
  .srch_bar .input-group-addon button {
	background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
	border: medium none;
	padding: 0;
	color: #707070;
	font-size: 18px;
  }
  .srch_bar .input-group-addon { margin: 0 0 0 -27px;}
  
  .chat_ib h5{ font-size:15px; color:#464646; margin:0 0 8px 0;}
  .chat_ib h5 span{ font-size:13px; float:right;}
  .chat_ib p{ font-size:14px; color:#989898; margin:auto}
  .chat_img {
	float: left;
	width: 11%;
  }
  .chat_ib {
	float: left;
	padding: 0 0 0 15px;
	width: 88%;
  }
  
  .chat_people{ overflow:hidden; clear:both;}
  .chat_list {
	border-bottom: 1px solid #c4c4c4;
	margin: 0;
	padding: 18px 16px 10px;
  }
  .inbox_chat { height: 550px; overflow-y: scroll;}
  
  .active_chat{ background:#ebebeb;}
  
  .incoming_msg_img {
	display: inline-block;
	width: 6%;
  }
  .received_msg {
	display: inline-block;
	padding: 0 0 0 10px;
	vertical-align: top;
	width: 92%;
   }
   .received_withd_msg p {
	background: #ebebeb none repeat scroll 0 0;
	border-radius: 3px;
	color: #646464;
	font-size: 14px;
	margin: 0;
	padding: 5px 10px 5px 12px;
	width: 100%;
  }
  .time_date {
	color: #747474;
	display: block;
	font-size: 12px;
	margin: 8px 0 0;
  }
  .received_withd_msg { width: 57%;}
  .mesgs {
	float: left;
	padding: 30px 15px 0 25px;
	width: 60%;
  }
  
   .sent_msg p {
	background: #05728f none repeat scroll 0 0;
	border-radius: 3px;
	font-size: 14px;
	margin: 0; color:#fff;
	padding: 5px 10px 5px 12px;
	width:100%;
  }
  .outgoing_msg{ overflow:hidden; margin:26px 0 26px;}
  .sent_msg {
	float: right;
	width: 46%;
  }
  .input_msg_write input {
	background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
	border: medium none;
	color: #4c4c4c;
	font-size: 15px;
	min-height: 48px;
	width: 100%;
  }
  
  .type_msg {border-top: 1px solid #c4c4c4;position: relative;}
  .msg_send_btn {
	background: #05728f none repeat scroll 0 0;
	border: medium none;
	border-radius: 50%;
	color: #fff;
	cursor: pointer;
	font-size: 17px;
	height: 33px;
	position: absolute;
	right: 0;
	top: 11px;
	width: 33px;
  }
  .messaging { padding: 0 0 50px 0;}
  .msg_history {
	height: 516px;
	overflow-y: auto;
  }

